#drupal-off-canvas,
#drupal-off-canvas-wrapper,
#drupal-off-canvas:not(.drupal-off-canvas-reset) {
	box-sizing: initial;

	.form-text,
	.form-tel,
	.form-email,
	.form-url,
	.form-search,
	.form-number,
	.form-color,
	.form-file,
	.form-textarea,
	.form-date,
	.form-time {
		background-color: inherit;
	}

	textarea,
	select,
	input[type='date'],
	input[type='datetime'],
	input[type='datetime-local'],
	input[type='email'],
	input[type='month'],
	input[type='number'],
	input[type='password'],
	input[type='search'],
	input[type='tel'],
	input[type='text'],
	input[type='time'],
	input[type='url'],
	input[type='week'] {
		border-radius: 6px;
	}

	input[type='submit'] {
		padding: 0.5rem;
		border-radius: 6px;
		font-family: inherit;
	}

	.field-add-more-submit {
		margin-top: 1rem;
		display: inline-block;
		background: #585858;
		padding: 0.25rem 0.5rem;
		border-radius: 6px;
		font-size: 14px;

		&:hover {
			background-color: #393939;
			text-decoration: none;
		}
	}

	tr {
		td,
		th {
			padding: 10px 4px;

			&.field-multiple-drag {
				width: inherit;
				padding-left: 10px;
			}
		}
	}

	a.tabledrag-handle {
		margin: 0;
		min-width: 14px;

		.handle {
			width: 14px;
			height: 14px;
			background-size: 100%;
			mask: none;
		}
	}

	.form-control {
		display: block;
		width: 100%;
		padding: 0.375rem 0.75rem;
		font-size: 1rem;
		font-weight: 400;
		line-height: 1.5;
		color: #212529;
		background-color: #fff;
		background-clip: padding-box;
		border: 1px solid #ced4da;
		appearance: none;
		border-radius: 0.375rem;
		transition: border-color 0.15s ease-in-out, box-shadow 0.15s ease-in-out;
	}

	.form-item {
		&.js-form-type-checkbox {
			padding-left: 0;
		}
	}
}
